import YouTube from "react-youtube";

import "../reset.css";
import { useState } from "react";
import { Container, Nav, Navbar, NavDropdown } from "react-bootstrap";
import { Routes, Route, Link, useNavigate, Outlet } from "react-router-dom";
import { hiphop, khiphop } from "../chart.js";
import "../hiphop.css";
function Hiphop(props) {
  let [list, setList] = useState(hiphop);
  let [list2, setList2] = useState(khiphop);
  return (
    <>
      <div className="body">
        <Navbar expand="lg" className="nav-bg">
          <Container id="nav-container">
            <Link to="/" className="logo">
              ActMusic
            </Link>
            <Navbar.Toggle aria-controls="basic-navbar-nav" />
            <Navbar.Collapse id="basic-navbar-nav">
              <Nav>
                <Link to="/hot" className="color-white link">
                  Hot
                </Link>

                <Link to="/Y2k" className="link">
                  Y2K
                </Link>

                <Link to="/hiphop" className="link">
                  Hiphop
                </Link>
              </Nav>
            </Navbar.Collapse>
          </Container>
        </Navbar>

        <div className="void">
          <div className="mainV-hiphop">
            <div className="mainV-text">
              <p id="mainV-text-t1">HipHop</p>
              <p id="mainV-text-t2">God's Plan (2018)</p>
              <p id="mainV-text-t3">
                힙합은 음악, 춤, 시, 미술 등을 포함하는 문화적 요소를 가진
                장르로, 1970년대 뉴욕의 아프리칸 아메리칸 및 라틴계 이민자들
                사이에서 시작되었습니다. 힙합 음악은 주로 랩과 비트 박스로
                구성되며, 솔로 아티스트나 그룹이 자신들의 이야기나 경험을
                전달하기 위해 가사를 사용합니다. 비트는 다양한 리듬과 샘플링을
                통해 만들어지며, 신나고 흥겨운 분위기를 조성합니다. 또한, 힙합은
                사회문화적 이슈를 다루는 데에도 많이 활용되며, 특정 집단이나
                사회적 주제에 대한 메시지를 전달하는 데 중요한 매체가 되기도
                합니다.
              </p>
            </div>
            <div className="youtubebox" id="mainV-App-youtube1">
              <YouTube
                videoId="xpVfcZ0ZcFM" //동영상 주소
                opts={{
                  width: "100%",
                  height: "500px",
                  playerVars: {
                    autoplay: 0, //자동 재생 여부
                    modestbranding: 1, //컨트롤 바에 유튜브 로고 표시 여부
                  },
                }}
                onReady={(e) => {
                  e.target.mute(); //소리 끔
                }}
              />
            </div>
          </div>
        </div>

        <div className="list">
          <h2 className="title">해외 힙합</h2>
          <div className="Music-list">
            {list.map(function (a, i) {
              return <Hiphoplist list={list[i]} i={i} key={i}></Hiphoplist>;
            })}
          </div>
        </div>

        <div className="list">
          <h2 className="title">국내 힙합</h2>
          <div className="Music-list">
            {list2.map(function (a, i) {
              return <Khiphoplist list={list2[i]} i={i} key={i}></Khiphoplist>;
            })}
          </div>
        </div>

        <footer></footer>
      </div>
    </>
  );
}

function Hiphoplist(props) {
  const imgUrl = process.env.PUBLIC_URL;
  let [modal, setModal] = useState(false);

  return (
    <div className="Music-Content">
      {/* -------------------------------이미지넘버↓*/}

      <div id="album-img">
        <p
          id="album-bg"
          onClick={() => {
            setModal(true);
          }}
        ></p>
        <img src={"/public_assets/hiphop" + (props.i + 1) + ".jpg"}></img>
      </div>
      <p className="t1">{props.list.title}</p>
      <p className="t2">{props.list.singer}</p>

      {/* <p className="t2">{props.music.content}</p> */}

      {modal === true ? (
        <div className="Modal">
          <div
            id="Modal-bg"
            onClick={() => {
              setModal(false);
            }}
          ></div>

          <div id="Modal-box">
            <YouTube
              videoId={props.list.videoid} //동영상 주소
              opts={{
                width: "100%",
                height: "550px",
                playerVars: {
                  autoplay: 0,
                  modestbranding: 1,
                },
              }}
              // onReady={(e) => {
              //   e.target.mute();
              // }}
            />
          </div>
        </div>
      ) : null}
    </div>
  );
}

