function About(props) {
  useEffect(() => {
    let timer = setTimeout(() => {
      setAlert(false);
    }, 2000);
    console.log(2);
    // useEffect가 실행되기전
    return () => {
      console.log(1);
      clearTimeout(timer);
    };
  }, []);

  const [alert, setAlert] = useState(true);
  let [count, setCount] = useState(0);

  let { id } = useParams();
  // let seacrGoods = props.music.find((x) => x.id == id);
  return (
    <div style={{ paddingTop: "150px", marginLeft: "5%" }}>
      <h2 style={{ fontSize: "33px" }}>찾으신 노래</h2>
      {alert == true ? (
        <div className="alert alert-warning">2초이내 구매시 할인</div>
      ) : null}
      {count}
      <button
        onClick={() => {
          setCount(count + 1);
        }}
      >
        +1 버튼
      </button>

      {/* nasted route */}

      <Outlet></Outlet>
    </div>
  );
}

function Anymore(props) {
  let [num, setNum] = useState("");

  useEffect(() => {
    if (isNaN(num) == true) {
      alert("숫자만 입력해주세요");
    }
  }, [num]);

  return (
    <input
      onChange={(e) => {
        setNum(e.target.value);
      }}
    />
  );
}

